import React, {lazy, Suspense} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Loading from "../../../components/shared-components/Loading";
import {Redirect, Route, Switch} from "react-router-dom";

const TravelViews = ({match})=> {
    return (
        <Suspense fallback={<Loading cover="content"/>}>
            <Switch>
                <Route path={`${ match.url }/all`} component={lazy(() => import(`./travel-all/index`))} />
                <Route path={`${ match.url }/recomendation`} component={lazy(()=> import("./travel-recomendation/index"))}/>
                <Route path={`${match.url}/p/:id`} component={lazy(()=> import("./travel-single/index"))}/>
                <Route path={`${match.url}/:slug/create-ticket`} component={lazy(()=> import("./travel-create-ticket/index"))}/>
                <Redirect to={`${match.url}/all`} from={match.url}/>
            </Switch>
        </Suspense>
    )
}

TravelViews.propTypes = {}
TravelViews.defaultProps = {}

export default connect(()=> {
    return {}
},{})(TravelViews)
